<template>
    <div class="quote-list">
      <div v-if="loading">
        <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" class="lds-spinner" width="200px" height="200px" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid" style="background: none;"><g transform="rotate(0 50 50)">
            <rect x="47" y="24" rx="9.4" ry="4.8" width="6" height="12" fill="#fefbfe">
              <animate attributeName="opacity" values="1;0" keyTimes="0;1" dur="1s" begin="-0.9166666666666666s" repeatCount="indefinite"/>
            </rect>
          </g><g transform="rotate(30 50 50)">
            <rect x="47" y="24" rx="9.4" ry="4.8" width="6" height="12" fill="#fefbfe">
              <animate attributeName="opacity" values="1;0" keyTimes="0;1" dur="1s" begin="-0.8333333333333334s" repeatCount="indefinite"/>
            </rect>
          </g><g transform="rotate(60 50 50)">
            <rect x="47" y="24" rx="9.4" ry="4.8" width="6" height="12" fill="#fefbfe">
              <animate attributeName="opacity" values="1;0" keyTimes="0;1" dur="1s" begin="-0.75s" repeatCount="indefinite"/>
            </rect>
          </g><g transform="rotate(90 50 50)">
            <rect x="47" y="24" rx="9.4" ry="4.8" width="6" height="12" fill="#fefbfe">
              <animate attributeName="opacity" values="1;0" keyTimes="0;1" dur="1s" begin="-0.6666666666666666s" repeatCount="indefinite"/>
            </rect>
          </g><g transform="rotate(120 50 50)">
            <rect x="47" y="24" rx="9.4" ry="4.8" width="6" height="12" fill="#fefbfe">
              <animate attributeName="opacity" values="1;0" keyTimes="0;1" dur="1s" begin="-0.5833333333333334s" repeatCount="indefinite"/>
            </rect>
          </g><g transform="rotate(150 50 50)">
            <rect x="47" y="24" rx="9.4" ry="4.8" width="6" height="12" fill="#fefbfe">
              <animate attributeName="opacity" values="1;0" keyTimes="0;1" dur="1s" begin="-0.5s" repeatCount="indefinite"/>
            </rect>
          </g><g transform="rotate(180 50 50)">
            <rect x="47" y="24" rx="9.4" ry="4.8" width="6" height="12" fill="#fefbfe">
              <animate attributeName="opacity" values="1;0" keyTimes="0;1" dur="1s" begin="-0.4166666666666667s" repeatCount="indefinite"/>
            </rect>
          </g><g transform="rotate(210 50 50)">
            <rect x="47" y="24" rx="9.4" ry="4.8" width="6" height="12" fill="#fefbfe">
              <animate attributeName="opacity" values="1;0" keyTimes="0;1" dur="1s" begin="-0.3333333333333333s" repeatCount="indefinite"/>
            </rect>
          </g><g transform="rotate(240 50 50)">
            <rect x="47" y="24" rx="9.4" ry="4.8" width="6" height="12" fill="#fefbfe">
              <animate attributeName="opacity" values="1;0" keyTimes="0;1" dur="1s" begin="-0.25s" repeatCount="indefinite"/>
            </rect>
          </g><g transform="rotate(270 50 50)">
            <rect x="47" y="24" rx="9.4" ry="4.8" width="6" height="12" fill="#fefbfe">
              <animate attributeName="opacity" values="1;0" keyTimes="0;1" dur="1s" begin="-0.16666666666666666s" repeatCount="indefinite"/>
            </rect>
          </g><g transform="rotate(300 50 50)">
            <rect x="47" y="24" rx="9.4" ry="4.8" width="6" height="12" fill="#fefbfe">
              <animate attributeName="opacity" values="1;0" keyTimes="0;1" dur="1s" begin="-0.08333333333333333s" repeatCount="indefinite"/>
            </rect>
          </g><g transform="rotate(330 50 50)">
            <rect x="47" y="24" rx="9.4" ry="4.8" width="6" height="12" fill="#fefbfe">
              <animate attributeName="opacity" values="1;0" keyTimes="0;1" dur="1s" begin="0s" repeatCount="indefinite"/>
            </rect>
          </g>
        </svg>
      </div>
      <div class="random-quote" v-else>
        <p>
            &#34;{{quotes[index].quote}}&#34;
        </p>
        - <em>{{quotes[index].author}}</em>
        <div id="quote-generate">
          <button class="btn btn-primary" v-on:click="nextQuote()">Update</button>
        </div>
      </div>
      <li><router-link to="/QuoteList">Add your own Rupaul Quotes</router-link></li>
    </div>
</template>
<script>

export default {
  name: 'random-quote',
  data () {
    return {
      loading: false,
      randomQuote: 0
    }
  },
  methods: {
    nextQuote () {
      // eslint-disable-next-line
      this.$store.dispatch('updateIndex').then(() => this.$store.dispatch('fetchQuotes'))
    }
  },
  computed: {
    quotes () {
      return this.$store.getters.rupaulQuotes
    },
    index () {
      return this.$store.getters.randomIndex
    }
  },

  created () {
    this.loading = true
    // eslint-disable-next-line
    this.$store.dispatch('fetchQuotes').then(() => {
      this.$store.dispatch('setIndex')
      this.loading = false
    })
  }
}
</script>

<style scoped>

.quote-list {
  box-sizing: border-box;
  width: 600px;
  height: fit-content;
  margin: auto;
  background: #fe67e3;
  padding: 10px 10px 80px 10px;
  margin-bottom: 100px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.random-quote {
  display: flex;
  flex-direction: column;
}
.random-quote btn {
  margin: 20px;
}
#quotes {
  transition-timing-function: ease-in;
  -webkit-transition: height 2s; /* Safari */
  transition: height 100s;
}
#quote-generate {
  padding: 20px 10px;
}
button {
  border-radius: 5px;
}
li {
  padding: 10px;
  width: 50%;
  margin: auto;
  border: solid 2px white;
  border-radius: 15px;
}
li a {
  color: white;
  text-decoration: none;
}
</style>



